module.exports = "<div *ngFor=\"let rarity of rarities\">\n  <span></span>\n  <h2 class=\"ui header\">\n    {{ getName(rarity) | prTitleCase }}\n    <span class=\"sub header\">\n      Completion:\n      {{ (ss.completion.byRarity | async)[rarity].any }}/{{ (ss.completion.byRarity | async)[rarity].target }}\n      <span class=\"percentage\">{{ getPercent((ss.completion.byRarity | async)[rarity], 'any') }}</span>\n    </span>\n  </h2>\n  <div class=\"ui padded collapsed grid\">\n    <div class=\"five column doubling collapsed row\">\n      <pr-card class=\"column\" [name]=\"card.name\" [rarity]=\"rarity\" [cardId]=\"card.id\"\n               [count]=\"getCount(collection | async, rarity, card.name, 'norm')\"\n               [goldCount]=\"getCount(collection | async, rarity, card.name, 'gold')\"\n               *ngFor=\"let card of (cards | async)[rarity]\"></pr-card>\n    </div>\n  </div>\n</div>\n<pr-legend>\n  <div class=\"four column doubling collapsed row\">\n    <pr-card class=\"column\" [name]=\"'Card'\" [count]=\"'(Regular Count)'\" [goldCount]=\"'(Golden Count)'\"></pr-card>\n  </div>\n  <div class=\"four column doubling collapsed row\">\n    <pr-card class=\"column legend\" [name]=\"'Zero Commons'\" [count]=\"0\" [rarity]=\"'comn'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'Zero Rares'\" [count]=\"0\" [rarity]=\"'rare'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'Zero Epics'\" [count]=\"0\" [rarity]=\"'epic'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'Zero Legendaries'\" [count]=\"0\" [rarity]=\"'lgnd'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'One Common'\" [count]=\"1\" [rarity]=\"'comn'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'One Rare'\" [count]=\"1\" [rarity]=\"'rare'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'One Epic'\" [count]=\"1\" [rarity]=\"'epic'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'At Least One Legendary'\" [count]=\"1\" [rarity]=\"'lgnd'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'At Least Two Commons'\" [count]=\"2\" [rarity]=\"'comn'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'At Least Two Rares'\" [count]=\"2\" [rarity]=\"'rare'\" [cost]=\"'norm'\"></pr-card>\n    <pr-card class=\"column legend\" [name]=\"'At Least Two Epics'\" [count]=\"2\" [rarity]=\"'epic'\" [cost]=\"'norm'\"></pr-card>\n  </div>\n</pr-legend>\n";

